import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home-equipment',
  templateUrl: './home-equipment.component.html',
  styleUrls: ['./home-equipment.component.scss']
})
export class HomeEquipmentComponent implements OnInit {

  constructor() { }

  imageClasses = ['first-image', 'second-image', 'third-image', 'fourth-image'];

  currentIndex = 0;
  currentImage = this.imageClasses[this.currentIndex];

  ngOnInit() {
  }

  handleChangeImage(index: number) {
    this.currentIndex += index;
    if ( this.currentIndex < 0) {
      this.currentIndex = this.imageClasses.length - 1;
    }
    if ( this.currentIndex >= this.imageClasses.length) {
      this.currentIndex = 0;
    }
    this.currentImage = this.imageClasses[this.currentIndex];
  }
}
